/* App.css */
body {
    font-family: 'Arial', sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f9f9f9;
    color: #333;
}

.navbar {
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 1rem;
    background-color: #4a90e2;
    /*position: -webkit-sticky;*/
    top: 0;
    z-index: 1000;
}

.nav-link {
    text-decoration: none;
    color: white;
    font-size: 1.2rem;
    padding: 0.5rem 1rem;
    transition: background-color 0.3s;
}

.nav-link:hover {
    background-color: #357ABD;
    border-radius: 5px;
}

.active-link {
    background-color: #357ABD;
    border-radius: 5px;
}

.content {
    padding: 2rem;
    margin: 1rem auto;
    max-width: 1200px;
}

.card {
    background: white;
    border-radius: 10px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 1.5rem;
    margin: 1rem 0;
    transition: transform 0.3s, box-shadow 0.3s;
}

.card:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

button {
    background-color: #4a90e2;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 0.5rem 1rem;
    cursor: pointer;
    transition: background-color 0.3s;
}

button:hover {
    background-color: #357ABD;
}

select {
    padding: 0.5rem;
    margin: 0.5rem 0;
    width: 100%;
    border-radius: 5px;
    border: 1px solid #ccc;
}

.item-details {
    margin-top: 20px;
    padding: 15px;
    border: 1px solid #ccc;
    border-radius: 5px;
    background-color: #f9f9f9;
}

.item-details h3 {
    margin-bottom: 10px;
    font-size: 1.5rem;
}

.item-details p {
    margin: 5px 0;
}

.item-details ul {
    list-style-type: none;
    padding: 0;
}

.item-details li {
    margin: 5px 0;
}

.server-selector {
    margin-left: auto;
    display: flex;
    align-items: center;
  }
  
  .server-dropdown {
    margin-left: 10px;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 4px;
  }
  